import { useSnackbar } from 'notistack'
import { useEffect, useRef, useState } from 'react'
import { Helmet } from 'react-helmet-async'
import { useNavigate, useParams } from 'react-router'
import { useRecoilValue } from 'recoil'

import { Card, Container, type Theme, useMediaQuery } from '@mui/material'
import type { Editor } from '@toast-ui/react-editor'

import { MANAGE_RECRUITMENT_MESSAGE } from 'src/data/messages'

import type { DateProps } from 'src/models/date'

import CancelSubmitBtns from 'src/components/CancelSubmitBtns'
import ConfirmDialog from 'src/components/ConfirmDialog'
import PageTitleWrapper from 'src/components/PageTitleWrapper'

import PageHeader from 'src/content/manage/PageHeader'
import ClubIntroduction from 'src/content/manage/Recruit/ClubIntroduction'
import RecruitInfo from 'src/content/manage/Recruit/RecruitInfo'

import { useCreateRecruit, useEditRecruit, useGetRecruits } from 'src/hooks/api/recruit'
import useSafeAuthUser from 'src/hooks/useSafeAuthUser'
import { recruitDateState } from 'src/recoil/atom'
import type { AddRecruitRequestData } from 'src/responses/recruit'

const newDate = new Date()

const RecruitEdit: React.FC = () => {
  const [newRecruitInfo, setNewRecruitInfo] = useState<AddRecruitRequestData>({
    title: '',
    allowedCollege: null,
    minGrade: null,
    maxGrade: null,
    startAt: null,
    endAt: null,
    limit: 0,
    contents: ''
  })
  const [isDialog, setIsDialog] = useState(false)
  const [registerRecruitId, setRegisterRecruitId] = useState(0)

  const { id, recruitId } = useParams()
  const clubId = Number(id)
  const recruitIdNumber = Number(recruitId)

  const navigate = useNavigate()

  const { userInfo } = useSafeAuthUser()
  const club = userInfo?.clubs.find(club => club.id === clubId)

  const createRecruit = useCreateRecruit()
  const { data, isSuccess } = useGetRecruits(clubId, recruitIdNumber)
  const editRecruit = useEditRecruit()

  const isUpperThanSm = useMediaQuery((theme: Theme) => theme.breakpoints.up('sm'))

  const { enqueueSnackbar } = useSnackbar()
  const editorRef = useRef<Editor>(null)

  useEffect(() => {
    if (recruitIdNumber) {
      if (isSuccess) {
        setNewRecruitInfo({
          title: data?.title || '',
          allowedCollege: data?.allowedCollege || null,
          minGrade: data?.minGrade || null,
          maxGrade: data?.maxGrade || null,
          startAt: data ? new Date(data.startAt) : newDate,
          endAt: data ? (data.endAt ? new Date(data.endAt) : null) : newDate,
          limit: data?.limit || 0,
          contents: data?.contents || ''
        })
      }
    }
  }, [isSuccess])

  const editRecruitDate = { year: data?.year, semester: data?.semester } as DateProps
  const date = recruitIdNumber ? editRecruitDate : useRecoilValue(recruitDateState)

  const handleSubmit = () => {
    if (!newRecruitInfo.startAt) {
      enqueueSnackbar(MANAGE_RECRUITMENT_MESSAGE.REQUIRE_DATE_SETTING, { variant: 'warning' })
      return
    }

    if (newRecruitInfo.minGrade && newRecruitInfo.maxGrade && newRecruitInfo.minGrade < newRecruitInfo.maxGrade) {
      enqueueSnackbar(MANAGE_RECRUITMENT_MESSAGE.WRONG_ORDER_UNIV_ENTERED_VALUE_MESSAGE, { variant: 'warning' })
      return
    }

    const endAt = newRecruitInfo.endAt ? new Date(newRecruitInfo.endAt) : null
    // endAt은 마감일 당일의 0시 0분 0초를 나타내므로 하루를 더해서 시작일과 비교
    // const endAtNextDay = new Date(endAt.getFullYear(), endAt.getMonth(), endAt.getDate() + 1)

    if (newRecruitInfo.endAt && newRecruitInfo.endAt < newRecruitInfo.startAt) {
      enqueueSnackbar(MANAGE_RECRUITMENT_MESSAGE.WRONG_ORDER_DATE_MESSAGE, { variant: 'warning' })
      return
    }

    if (newRecruitInfo.limit > 999) {
      enqueueSnackbar(MANAGE_RECRUITMENT_MESSAGE.PASSED_LIMIT_MESSAGE, { variant: 'warning' })
      return
    }
    if (newRecruitInfo.limit < 0) {
      enqueueSnackbar(MANAGE_RECRUITMENT_MESSAGE.MINUS_VALUE_MESSAGE, { variant: 'warning' })
      return
    }
    /**
     * 그 날 밤 23:59:59까지 신청을 받아야 함.
     * 프론트에서는 날짜만 지정하고, 시 분 초는 모두 0으로 설정(이 때는 한국 시간)
     * api 통신할 때는 같은 시간이지만, UTC 시간으로 요청 보냄(ex. 2023-08-01T15:00:00.000Z)
     * 백엔드 디비는 타임존 개념이 없어서 8월 1일 15시로 그대로 저장이 됌.
     * 그래서 로직으로 +1일 -1초 한 후 DB에 저장
     * 그럼 DB에 8월 2일 14:59:59가 저장이 됨.
     * 이후 프론트의 동아리 소개 페이지에서 8월 2일 14:59:59를 불러온 후,
     * 브라우저가 알아서 +9를 해서 기한을 맞출 수 있음.
     */
    const utcNewRecruitInfo = newRecruitInfo
    if (endAt) {
      endAt.setDate(endAt.getDate() - 1)
      utcNewRecruitInfo.endAt = endAt
    }

    if (recruitIdNumber) {
      editRecruit.mutate(
        {
          clubId: clubId,
          data: { ...utcNewRecruitInfo, contents: editorRef.current?.getInstance().getHTML() ?? '' },
          recruitId: recruitIdNumber
        },
        {
          onSuccess: () => navigate(`/manage/${clubId}/recruit`)
        }
      )
      return
    } else {
      createRecruit.mutate(
        {
          clubId: clubId,
          data: { ...utcNewRecruitInfo, contents: editorRef.current?.getInstance().getHTML() ?? '' },
          date: date
        },
        {
          onSuccess: response => {
            setIsDialog(true)
            setRegisterRecruitId(response)
          }
        }
      )
    }
  }

  const handleChange = (name: string, value: Date | null | number | string) => {
    setNewRecruitInfo(prevState => ({
      ...prevState,
      [name]: value
    }))
  }

  const handleAlwaysRecruitChange = (value: boolean) => {
    setNewRecruitInfo(prev => ({
      ...prev,
      endAt: value ? null : data?.endAt ? new Date(data?.endAt) : newDate
    }))
  }

  const handleFormRegister = () => {
    setIsDialog(false)
    navigate(`/manage/${clubId}/recruit/${registerRecruitId}/form`)
  }
  const handleCloseFormRegister = () => {
    setIsDialog(false)
    navigate(`/manage/${clubId}/recruit`)
  }

  return (
    <>
      <Helmet>
        <title>{recruitIdNumber ? '모집공고 수정' : '모집공고 등록'}</title>
      </Helmet>
      <PageTitleWrapper>
        <PageHeader clubName={club?.name ?? ''} />
      </PageTitleWrapper>
      <Container maxWidth="lg" sx={{ px: isUpperThanSm ? 2 : 0 }}>
        <Card sx={{ padding: { xs: 2, md: 4 } }}>
          <RecruitInfo
            year={date?.year}
            semester={date?.semester}
            newRecruitInfo={newRecruitInfo}
            handleChange={handleChange}
            handleAlwaysRecruitChange={handleAlwaysRecruitChange}
          />
          <ClubIntroduction content={recruitIdNumber ? data?.contents || '' : ''} editorRef={editorRef} />
          <CancelSubmitBtns handleSubmit={handleSubmit} submitBtnText={recruitIdNumber ? '수정' : '등록'} />
        </Card>
      </Container>
      <ConfirmDialog
        open={isDialog}
        handleSubmit={handleFormRegister}
        handleClose={handleCloseFormRegister}
        confirmMessage="모집공고가 정상적으로 등록되었습니다. 지원서 문항을 이어서 작성하시겠습니까?"
      />
    </>
  )
}

export default RecruitEdit
